@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
.home-container {
  padding-bottom: 50px;
}

.h1 {
  height: 100%;
}

.title {
  text-align: center;
  padding-top: 10%;
}

.title h1 {
  font-family: "Roboto Mono", monospace;
  font-size: 4em;
}

.title p {
  text-align: center;
  font-size: 2em;
}

.start-button {
  text-align: center;
  margin-top: 50px !important;
  font-size: 3em !important;
  font-family: "Roboto Mono", monospace !important;
}

.start-button {
  cursor: pointer;
}

.game {
  padding: 10px 0;
}

.score {
  text-align: center;
  font-family: "Roboto Mono", monospace !important;
  margin-top: 10px !important;
}

.capital-city {
  text-align: center;
  font-family: "Roboto Mono", monospace;
  font-size: 50px !important;
}

.button {
  border-radius: 0 !important;
}

.score-container {
  padding-bottom: 50px;
}

.final-score {
  text-align: center;
  padding-top: 10%;
}

.final-score h1 {
  font-family: "Roboto Mono", monospace;
  font-size: 4em;
}

.start-over-button {
  text-align: center;
  margin-top: 50px !important;
  font-size: 3em !important;
  font-family: "Roboto Mono", monospace !important;
}

.start-over-button {
  cursor: pointer;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@media screen and (max-width: 600px) {
  .capital-city {
    text-align: center;
    font-family: "Roboto Mono", monospace;
    font-size: 2.2em !important;
  }
}

.country {
  background-color: rgba(240, 240, 240, 100) !important;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  border: 1px solid #000000 !important;
  margin: 25px;
  height: 120px;
  line-height: 1.5em;
  font-size: 1.2vw;
  font-family: "Ubuntu", sans-serif;
}

@media (hover: hover) {
  .country:hover {
    cursor: pointer;
    background-color: green !important;
    color: white;
  }
}

.flag {
  width: 4rem;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@media screen and (max-width: 600px) {
  .country {
    height: 60px;
    font-size: 8pt !important;
  }

  .flag {
    width: 2.5rem;
  }

  .score {
    font-size: 11pt !important;
  }
}

.leaflet-container {
  margin: 10px 0 15px 0 !important;
  width: 100%;
  height: 40vh;
  margin: 0 !important;
  border: 1px solid black;
}

.info {
  margin: 3px 0;
}

.info p {
  margin: 10px 5px;
}

@media screen and (max-width: 600px) {
  .info {
    width: 150px;
    white-space: normal;
  }
}

